*, *::before, *::after {
  box-sizing: inherit;
}

* {
  margin: 0;
  padding: 0;
  font: inherit;
}

html {
  font-family: "Space Mono", monospace;
  font-weight: 700;
  font-size: 100%;
  box-sizing: border-box;
}

body {
  min-height: 100vh;
  font-size: 1.5rem;
  background-color: hsl(185deg, 41%, 84%);
  display: flex;
  flex-direction: column;
}
@media (max-width: 87.4375em) {
  body {
    font-size: 1.125rem;
  }
}

ul {
  list-style: none;
}

img, picture, svg, video {
  display: block;
  height: auto;
  max-width: 100%;
}

iframe {
  border: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

td, th {
  padding: 0;
}

input {
  color: hsl(183deg, 100%, 15%);
  border: 0.0625rem solid hsl(189deg, 41%, 97%);
  background-color: hsl(189deg, 41%, 97%);
  border-radius: 0.5rem;
}
input::placeholder {
  color: hsl(184deg, 14%, 56%);
}
input:focus {
  outline: none;
  border: 0.0625rem solid hsl(172deg, 67%, 45%);
}

.sr-only {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

h1, h2, h3 {
  margin: 0;
}

p {
  margin: 0;
}

a, a:visited, a:active {
  text-decoration: none;
}

main {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media (max-width: 48.6875em) {
  main {
    height: auto;
    padding-top: 2.5rem;
  }
}
main h1 {
  font-size: 1.5rem;
  color: hsl(183deg, 100%, 15%);
  overflow-wrap: break-word;
  width: 6.25rem;
  letter-spacing: 0.625rem;
  line-height: 1.5;
  margin-bottom: 4.875rem;
  margin-left: 0.5rem;
}
@media (max-width: 87.4375em) {
  main h1 {
    font-size: 1.125rem;
    margin-bottom: 3.75rem;
  }
}
@media (max-width: 48.6875em) {
  main h1 {
    margin-left: 0;
    font-size: 1.5rem;
    margin-left: 0.5rem;
    margin-bottom: 1.875rem;
  }
}
main .container {
  background-color: hsl(0deg, 0%, 100%);
  padding: 2.25rem 2.375rem 2.25rem 3rem;
  display: flex;
  width: 57.8125rem;
  margin-bottom: 3.75rem;
  border-radius: 1.375rem;
  column-gap: 1.25rem;
}
@media (max-width: 87.4375em) {
  main .container {
    padding: 1.75rem 1.8125rem 1.75rem 2.3125rem;
    width: 44.5rem;
    margin-bottom: 2.875rem;
    column-gap: 0.9375rem;
  }
}
@media (max-width: 48.6875em) {
  main .container {
    flex-direction: column;
    width: 80%;
    padding: 1.625rem;
  }
}
@media (max-width: 39.9375em) {
  main .container {
    width: 100%;
  }
}
main .container__left {
  flex: 1;
  margin-top: 0.625rem;
}
@media (max-width: 87.4375em) {
  main .container__left {
    margin-top: 0.5rem;
  }
}
@media (max-width: 48.6875em) {
  main .container__left {
    margin-top: 0;
  }
}
main .container__left-2 {
  padding-right: 1.875rem;
}
@media (max-width: 87.4375em) {
  main .container__left-2 {
    padding-right: 1.4375rem;
  }
}
@media (max-width: 48.6875em) {
  main .container__left-2 {
    padding-right: 0;
  }
}
main .container__left-2 .input {
  display: flex;
  flex-direction: column;
  position: relative;
  margin-bottom: 2.5rem;
}
@media (max-width: 87.4375em) {
  main .container__left-2 .input {
    margin-bottom: 1.9375rem;
  }
  main .container__left-2 .input:not(:first-child) {
    margin-bottom: 0.6875rem;
  }
}
@media (max-width: 48.6875em) {
  main .container__left-2 .input {
    margin-bottom: 1.75rem !important;
  }
}
main .container__left-2 .input:not(:first-child) {
  margin-bottom: 0.875rem;
}
main .container__left-2 .input.error input {
  border: 1px solid red;
}
main .container__left-2 .input__label {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  margin-bottom: 0.5rem;
}
@media (max-width: 87.4375em) {
  main .container__left-2 .input__label {
    margin-bottom: 0.375rem;
  }
}
main .container__left-2 .input__label label {
  font-size: 1rem;
  color: hsl(186deg, 14%, 43%);
}
@media (max-width: 87.4375em) {
  main .container__left-2 .input__label label {
    font-size: 0.75rem;
  }
}
main .container__left-2 .input__label small {
  font-size: 0.8125rem;
  color: hsl(0deg, 73%, 54%);
}
@media (max-width: 87.4375em) {
  main .container__left-2 .input__label small {
    font-size: 0.625rem;
  }
}
main .container__left-2 .input i {
  background-image: url("/images/icon-dollar.svg");
  width: 0.6875rem;
  height: 1.0625rem;
  position: absolute;
  top: 2.9375rem;
  left: 1rem;
}
@media (max-width: 87.4375em) {
  main .container__left-2 .input i {
    width: 0.5rem;
    height: 0.8125rem;
    background-size: contain;
    top: 2.25rem;
    left: 0.75rem;
  }
}
main .container__left-2 .input input {
  text-align: right;
  padding: 0.375rem 1rem 0.375rem 2.25rem;
  width: 100%;
}
@media (max-width: 87.4375em) {
  main .container__left-2 .input input {
    padding: 0.3125rem 0.75rem 0.3125rem 1.75rem;
  }
}
main .container__left-2 .select-Tip {
  margin-bottom: 2.5rem;
}
@media (max-width: 87.4375em) {
  main .container__left-2 .select-Tip {
    margin-bottom: 1.9375rem;
  }
}
main .container__left-2 .select-Tip p {
  font-size: 1rem;
  color: hsl(186deg, 14%, 43%);
  margin-bottom: 0.875rem;
}
@media (max-width: 87.4375em) {
  main .container__left-2 .select-Tip p {
    font-size: 0.75rem;
    margin-bottom: 0.6875rem;
  }
}
main .container__left-2 .select-Tip__container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 0.75rem;
  row-gap: 1rem;
}
@media (max-width: 87.4375em) {
  main .container__left-2 .select-Tip__container {
    gap: 0.5625rem;
    row-gap: 0.75rem;
  }
}
@media (max-width: 39.9375em) {
  main .container__left-2 .select-Tip__container {
    grid-template-columns: repeat(2, 1fr);
    gap: 0.75rem;
  }
}
main .container__left-2 .select-Tip__container button {
  background-color: hsl(183deg, 100%, 15%);
  border: none;
  color: hsl(0deg, 0%, 100%);
  padding: 0.375rem 0;
  border-radius: 0.375rem;
  cursor: pointer;
}
@media (max-width: 87.4375em) {
  main .container__left-2 .select-Tip__container button {
    padding: 0.3125rem 0;
  }
}
@media (max-width: 39.9375em) {
  main .container__left-2 .select-Tip__container button {
    padding: 0.4375rem 0;
  }
}
main .container__left-2 .select-Tip__container button:hover {
  background-color: hsl(185deg, 41%, 84%);
  color: hsl(183deg, 100%, 15%);
}
main .container__left-2 .select-Tip__container .tipBtn.active {
  background-color: hsl(172deg, 67%, 45%);
  color: hsl(183deg, 100%, 15%);
}
main .container__left-2 .select-Tip__container input {
  width: 100%;
  text-align: center;
}
main .container__right {
  flex: 1;
  background-color: hsl(183deg, 100%, 15%);
  border-radius: 1rem;
}
@media (max-width: 87.4375em) {
  main .container__right {
    border-radius: 0.75rem;
  }
}
main .container__right-container2 {
  padding: 2.25rem;
  display: flex;
  flex-direction: column;
  height: 100%;
}
@media (max-width: 87.4375em) {
  main .container__right-container2 {
    padding: 1.75rem;
  }
}
@media (max-width: 48.6875em) {
  main .container__right-container2 {
    padding: 1.5rem;
  }
}
main .container__right-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
main .container__right-container:first-child {
  margin-bottom: 1.625rem;
}
@media (max-width: 87.4375em) {
  main .container__right-container:first-child {
    margin-bottom: 1.25rem;
  }
}
main .container__right-left .p1 {
  font-size: 1rem;
  color: hsl(0deg, 0%, 100%);
}
@media (max-width: 87.4375em) {
  main .container__right-left .p1 {
    font-size: 0.75rem;
  }
}
main .container__right-left .p2 {
  font-size: 0.8125rem;
  color: hsl(184deg, 14%, 56%);
}
@media (max-width: 87.4375em) {
  main .container__right-left .p2 {
    font-size: 0.625rem;
  }
}
main .container__right-right .price {
  font-size: 3rem;
  color: hsl(172deg, 67%, 45%);
}
@media (max-width: 87.4375em) {
  main .container__right-right .price {
    font-size: 2.3125rem;
  }
}
main .container__right button {
  margin-top: auto;
  border: none;
  background-color: hsl(172deg, 67%, 45%);
  color: hsl(183deg, 100%, 15%);
  padding: 0.375rem 0;
  cursor: pointer;
  border-radius: 0.25rem;
}
@media (max-width: 87.4375em) {
  main .container__right button {
    padding: 0.3125rem 0;
  }
}
@media (max-width: 48.6875em) {
  main .container__right button {
    margin-top: 0.875rem;
  }
}
main .container__right button:hover {
  background-color: hsl(185deg, 41%, 84%);
}
main .container__right button.inactive {
  opacity: 0.5;
  cursor: default;
}
main .container__right button.inactive:hover {
  background-color: hsl(172deg, 67%, 45%);
}

footer {
  margin-top: auto;
}
footer .attribution {
  font-size: 11px;
  text-align: center;
}
footer .attribution a {
  color: hsl(228deg, 45%, 44%);
}